.block_text_hide {
	margin-bottom: rem(108);

	.title_24 {
		margin-bottom: rem(13);
	}

	.text {
		font-size: rem(16);
		line-height: 140%;
		color: var(--c-gray-title);

		margin-bottom: rem(30);

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	.button_hide {
		display: flex;
		align-items: center;
		margin-top: rem(64);


		span {
			font-size: rem(16);
			font-weight: 500;
			color: var(--c-gray-title);
			margin-right: rem(4);
		}

		svg {

		}
	}
}

.block_text_hide.hide {
	.text {
		display: none;
	}

	.button_hide {
		
		> span {
			
			.tt {
				display: none;
			}

			&::before {
				content: "";
			}
		}
		
		svg {
			transform: rotate(180deg);
		}
	}
}