.slider_price {

	margin-bottom: rem(140);

	.swiper-control {
		display: flex;
		justify-content: space-between;
		padding: rem(32) 0;

		
	}

	.item {
		display: flex;
		background-color: #141105;
		height: rem(460);

		&__left, &__right {
			flex: 0 0 50%;
			max-width: 100%;
		}

		&__left {
			padding: rem(90) rem(124);

			display: flex;
			flex-direction: column;

			.title {
				display: flex;
				align-items: center;

				font-family: var(--fontBrygada);
				font-weight: bold;
				font-size: rem(14);
				letter-spacing: rem(3.4);
				text-transform: uppercase;
				color: var(--c-accent2);

				margin-bottom: rem(20);

				&::before {
					content: "";
					width: rem(80);
					height: rem(1.2);
					background-color: #FFB571;
					margin-right: rem(8);
				}
			}

			.text {
				font-size: rem(24);
				color: white;
				font-weight: 500;
				line-height: 156%;
				margin-bottom: rem(36);
			}

			.button_border {
				align-self: flex-start;
			}
		}

		&__right {
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

}