@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

@font-face {
	font-family: 'Spartan';
	src: local('Spartan Thin'),
		local('Spartan-Thin'),
		url('../fonts/Spartan/Spartan-Thin.woff2') format('woff2');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

