.calc_wrapper {
	margin-bottom: rem(103);

	.title_36 {
		margin-bottom: rem(13);
	}

	.text {
		font-size: rem(16);
		line-height: 173%;
		color: var(--c-gray-title);
	}

	&__form {
		margin-top: rem(60);

		display: flex;

		height: rem(593);

		.left {
			position: relative;
			flex: 0 0 rem(380);
			max-width: rem(380);
			padding: rem(55) rem(48);
			display: flex;
			flex-direction: column;

			box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
			0px 4px 50px rgba(0, 0, 0, 0.4);

			border-radius: rem(5);
			overflow: hidden;

			background: url(../img/left_bg_calc.jpg) no-repeat;
			background-size: cover;

			&__title {
				position: relative;
				z-index: 2;
				font-size: rem(24);
				font-family: var(--fontBrygada);
				color: white;
				font-weight: 600;
				font-style: italic;
				margin-bottom: rem(15);
			}

			&__text {
				position: relative;
				z-index: 2;
				font-size: rem(13);
				color: white;
				max-width: rem(219);
				line-height: 160%;
			}

			&__top_svg {
				position: absolute;
				top: 0;
				left: 0;
				z-index: 0;
			}

			&__image {
				position: absolute;
				z-index: 1;
				bottom: 0;
				left: 0;
			}

			&__radial {
				position: absolute;
				bottom: 0;
				left: 0;
				z-index: 0;
			}

			&__radial_final {
				position: absolute;
				bottom: 0;
				left: 0;
				z-index: 0;
				display: none;
			}
		}

		.left.final {

			align-items: center;
			justify-content: center;

			.left__text,
			.left__image,
			.left__top_svg,
			.left__radial {
				display: none;
			}
		

			.left__radial_final {
				display: block;
			}

			.left__title {
				text-align: center;
			}
		}
	
		.right {
			position: relative;
			overflow: hidden;
			flex: 1;
			min-width: 0;
			padding: rem(24) rem(62) rem(44) rem(62);
			display: flex;
			flex-direction: column;

			margin-left: rem(30);

			box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
			0px 4px 50px rgba(0, 0, 0, 0.4);
			background: linear-gradient(180deg, #FFFFFF 0%, #F5F5F5 100%);

			border-radius: rem(5);

			.swiper {
				margin: rem(-20);
				padding: rem(20);
			}

			.final_right_bg {
				position: absolute;
				top: 0;
				right: 0;

				display: none;

				@include min1440 {
					right: rem(-120);
				}
			}
			.final_right_bg.active {
				display: block;
			}

			&__page {
				margin-top: auto;

				display: flex;
				align-items: center;

				height: rem(50);

				.swiper-button-disabled {
					display: none;
				}

				&_prev {
					margin-right: rem(34);

					span {
						font-size: rem(16);
						text-transform: uppercase;
						font-weight: bold;
						font-family: var(--fontBrygada);
						color: var(--c-black-40);
					}
				}

				&_next {
					position: relative;
					background-color: #DADADA;
					border-radius: rem(5);
					height: rem(50);

					display: flex;
					align-items: center;

					padding: 0 rem(35);

					svg {
						position: absolute;
						top: 0;
						right: 0;
						z-index: 2;
						height: rem(50);
					}

					&::after {
						content: "";
						position: absolute;
						bottom: rem(-4);
						left: rem(5);
						border-bottom-left-radius: rem(25);
						border-bottom-right-radius: rem(25);
						height: rem(4);
						width: calc(100% - 10px);

						background-color: #AEAEAE;

						// z-index: -1;
					}

					&::before {
						content: "";
						position: absolute;
						width: rem(108);
						height: rem(25);
						z-index: 2;
						top: 0;
						left: 0;
						background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0.51%, #FFFFFF 49.48%, rgba(255, 255, 255, 0) 100%);
						filter: blur(30.5px);
						transform: rotate(-52.36deg);
					}

					span {
						position: relative;
						z-index: 2;
						color: #303030;
						font-size: rem(16);
						text-transform: uppercase;
						font-weight: bold;
						font-family: var(--fontBrygada);
					}
				}

				&_next.button_orange {
					background: linear-gradient(90deg, #FFB571 0%, #FFC997 100%);
				
					&::after {
						background-color: #A37850;
					}
				}
			}

			&__nav {
				display: flex;
				align-items: center;

				&_text {
					margin-right: rem(40);

					// width: rem(80);
					flex: 0 0 rem(80);
					max-width: rem(80);

					span {
						white-space: nowrap;
						font-family: var(--fontBrygada);
						color: #979797;
						text-transform: uppercase;
					}

					.current {

					}
				}

				

				.bullet {
					display: flex;
					align-items: center;
				}

				.swiper-pagination-bullet {
					position: relative;
					width: rem(20);
					height: rem(20);
					// border: rem(0.5) solid rgba(0,0,0,.1);
					border: rem(0.5) solid #FFB571;
					background-color: transparent;
					text-indent: -9999em;

					display: flex;
					align-items: center;
					justify-content: center;

					opacity: 1;

					margin: 0 rem(30) 0 0 !important;

					&:last-child {
						&::after {
							display: none;
						}
					}
					
					&::after {
						content: "";
						position: absolute;
						top: 50%;
						right: rem(-24);
						width: rem(17);
						height: rem(1);
						background-color: #FFB571;
					}

					&::before {
						content: "";

						width: rem(12);
						height: rem(12);
						border-radius: 50%;
						background-color: #FFB571;
					}
				}

				.swiper-pagination-bullet-active {
					border: rem(0.5) solid #FFB571;

					&::before {
						background-color: #FFB571;
					}
					&::after {
						background-color: #FFB571;
					}

					~ span {
						border: rem(0.5) solid rgba(0, 0, 0, .1);

						&::before {
							background-color: #DBDBDB;
						}

						&::after {
							background-color: #DBDBDB;
						}
					}
				}
			}

			&__nav.hide {
				display: none;
			}

			&__title {
				margin-top: rem(22);
				margin-bottom: rem(100);

				.text {
					color: #212121;
					font-size: rem(36);
					font-family: var(--fontBrygada);
					font-weight: bold;
					font-style: italic;
				}
			}

			.swiper {
				width: 100%;
			}
		}
	}
}

.calc_step_1 {
	display: flex;
	// flex-wrap: wrap;
	justify-content: space-between;

	.item {
		cursor: pointer;

		display: flex;
		flex-direction: column;
		align-items: center;

		.image {
			height: rem(82);
			margin-bottom: rem(36);
		}

		input {
			display: none;

			&:checked {
				~ .flex {
					.check {
						svg {
							circle {
								&:first-of-type {
									fill: #FFB571;
								}
							}
						}
					}
				}
			}
		}

		.flex {
			align-items: center;
		}

		.check {
			width: rem(17);
			height: rem(17);
			margin-right: rem(10);

			svg {
				circle {
					&:first-of-type {
						fill: #B8B8B8;
					}
					
				}
			}
		}

		.text {
			font-size: rem(16);
			color: #212121;
			white-space: nowrap;
		}
	}

	.item.item_last {
		.image {
			img {
				margin-top: rem(20);
			}
		}
	}
}

.calc_step_2 {
	display: flex;
	// flex-wrap: wrap;
	justify-content: space-between;

	.item {

		display: flex;
		flex-direction: column;
		align-items: center;

		.image {
			height: rem(82);
			margin-bottom: rem(36);
		}

		&__control {
			display: flex;
			flex-direction: column;
			align-items: center;

			.text {
				margin-bottom: rem(20);
				
				b {
					font-size: rem(16);
					line-height: 160%;
					font-weight: 600;
					color: #212121;

					&::after {
						content: attr(data-text);
						margin-left: rem(6);
						font-size: rem(16);
						line-height: 160%;

						color: rgba(0,0,0,.3);
					}
				}
			}

			.number {
				position: relative;
				width: rem(125);

				button {
					position: absolute;
					top: rem(4);
				}

				.minus {
					left: rem(4);
				}
				.plus {
					right: rem(4);
				}

				.field {
					width: rem(125);
					height: rem(35);
					border-radius: rem(5);
					background-color: white;
					border: rem(1) solid #C1C1C1;
					text-align: center;

					color: rgba(33,33,33,.5);

					-webkit-appearance: none;
					-moz-appearance:textfield;

					@include placeholder {
						color: rgba(33, 33, 33, .5);
					}
				}

				.field::-webkit-outer-spin-button,
				.field::-webkit-inner-spin-button {
					-webkit-appearance: none;
				}
			}
		}

		input[type="radio"] {
			display: none;

			&:checked {
				~.flex {
					.check {
						svg {
							circle {
								&:first-of-type {
									fill: #FFB571;
								}
							}
						}
					}
				}
			}
		}

		.flex {
			align-items: center;

			.check {
				width: rem(17);
				height: rem(17);
				margin-right: rem(10);

				svg {
					circle {
						&:first-of-type {
							fill: #B8B8B8;
						}

					}
				}
			}

			.text {
				font-size: rem(16);
				color: #212121;
				white-space: nowrap;
			}
		}

		
	}

	label.item {
		cursor: pointer;
	}

	.item.item_last {
		.image {
			margin-bottom: rem(58);
			img {
				margin-top: rem(20);
			}
		}
	}
}

.calc_step_3 {

	display: flex;
	justify-content: space-between;

	.item {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;

		.image {
			width: rem(176);
			height: rem(176);

			display: flex;
			align-items: center;
			justify-content: center;
			background-color: white;
			border-radius: rem(10);
			border: rem(0.5) solid rgba(0, 0, 0, .2);

			margin-bottom: rem(35);

			@include min1440 {
				width: rem(136);
				height: rem(136);
			}
		}

		input {
			display: none;

			&:checked {
				~.image {
					box-shadow: 0px 4px 25px rgba(255, 181, 113, 0.79);
					border: 0;
				}

				~.check {
					svg {
						circle {
							fill: #FFB571;
						}
					}
				}
			}
		}

		.check {
			position: absolute;

			bottom: rem(46);
			left: 50%;
			margin-left: rem(-13);

			width: rem(34);
			height: rem(34);
			border-radius: 50%;
			background-color: white;

			display: flex;
			align-items: center;
			justify-content: center;

			svg {
				circle {
					fill: #5C5C5C;
				}
			}
		}

		.text {
			font-size: rem(16);
			font-weight: 600;
			color: #212121;
		}
	}

	.item_last {
		cursor: pointer;
		display: flex;
		flex-direction: column;
		align-items: center;

		.image {
			height: rem(82);
			margin-top: rem(34);
			margin-bottom: rem(18);
		}

		

		input[type="radio"] {
			display: none;

			&:checked {
				~.flex {
					.check {
						svg {
							circle {
								&:first-of-type {
									fill: #FFB571;
								}
							}
						}
					}
				}
			}
		}

		.flex {
			align-items: center;

			.check {
				width: rem(17);
				height: rem(17);
				margin-right: rem(10);

				svg {
					circle {
						&:first-of-type {
							fill: #B8B8B8;
						}

					}
				}
			}

			.text {
				font-size: rem(16);
				color: #212121;
				white-space: nowrap;
			}
		}


	}

	label.item {
		cursor: pointer;
	}

	.item.item_last {
		.image {
			margin-bottom: rem(58);

			img {
				margin-top: rem(20);
			}
		}
	}
}

.calc_step_4 {
	position: relative;
	.right__subtitle {
		color: #979797;
		font-family: var(--fontBrygada);
		text-transform: uppercase;

		margin-bottom: rem(125);
	}

	&__form {
		.right__title {
			margin-bottom: rem(15);
		}

		.field {
			box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.1);
			border-radius: rem(5);
			height: rem(54);
			width: rem(300);
			padding: 0 rem(20);

			display: flex;
			align-items: center;

			font-size: rem(16);
			font-weight: 500;
			color: black;

			border: rem(1) solid #FFB571;

			margin-bottom: rem(10);

			@include placeholder {
				color: black;
			}
		}

		.button {
			width: rem(300);

			position: relative;
			border-radius: rem(5);
			height: rem(50);

			display: flex;
			align-items: center;

			padding: 0 rem(35);

			margin-bottom: rem(34);

			background: linear-gradient(90deg, #FFB571 0%, #FFC997 100%);

			svg {
				position: absolute;
				top: 0;
				right: 0;
				z-index: 2;
				height: rem(50);
			}

			&::after {
				content: "";
				position: absolute;
				bottom: rem(-4);
				left: rem(5);
				border-bottom-left-radius: rem(25);
				border-bottom-right-radius: rem(25);
				height: rem(4);
				width: calc(100% - 10px);

				background-color: #A37850;

				// z-index: -1;
			}

			&::before {
				content: "";
				position: absolute;
				width: rem(108);
				height: rem(25);
				z-index: 2;
				top: 0;
				left: 0;
				background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0.51%, #FFFFFF 49.48%, rgba(255, 255, 255, 0) 100%);
				filter: blur(30.5px);
				transform: rotate(-52.36deg);
			}

			span {
				position: relative;
				z-index: 2;
				color: #303030;
				font-size: rem(16);
				text-transform: uppercase;
				font-weight: bold;
				font-family: var(--fontBrygada);
			}
		}

		.field_checked {
			align-items: flex-start;
			width: rem(300);

			.check {
				border: rem(1) solid #333333;
				margin-top: rem(5);
			}

			.text {
				white-space: normal;
				color: #333333;
			}
		}
	}
}