.review__item {
	position: relative;
	display: flex;
	flex-direction: column;

	height: rem(420);

	padding: rem(37) rem(25) rem(30) rem(25);
	background-color: white;

	&::before {
		content: "";
		position: absolute;
		top: rem(-16);
		left: rem(-16);
		width: calc(100% + 32px);
		height: calc(100% + 32px);
		border: rem(1) solid #FFB571;
		z-index: -1;
		transition: var(--transition);
		opacity: 0;
		pointer-events: none;
	}

	&:hover {
		background-color: var(--c-gray-title);
		box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
		0px 4px 50px rgba(0, 0, 0, 0.4);

		&::before {
			opacity: 1;
			pointer-events: visible;
		}

		.review__item_text,
		.review__item_bottom {
			p,b {
				color: white;
			}
		}
	}

	&_icon {
		margin-bottom: rem(68);
	}

	&_text {
		p {
			transition: var(--transition);
			font-family: var(--fontBrygada);
			line-height: rem(22);
		}
	}

	&_bottom {
		margin-top: auto;

		b {
			transition: var(--transition);
			font-size: rem(12);
		}
	}
}