.header {
	height: rem(965);
	width: 100%;

	background: url(../img/header_bg.jpg) no-repeat top center;
	background-size: cover;

	margin-bottom: rem(113);

	&__abs {
		margin-top: rem(150);

		display: flex;
		justify-content: center;
	}

	&__top {
		height: rem(44);
		background-color: black;

		.container {
			height: 100%;
			display: flex;
			align-items: center;
			flex-wrap: wrap;
			justify-content: space-between;
		}

		&_left {
			display: flex;
			align-items: center;

			.drop_menu {
				position: relative;
				margin-right: rem(30);

				&__list {
					position: absolute;
					top: rem(42);
					left: rem(-5);
					z-index: 2;
					background-color: rgba(255,255,255,.99);
					padding: rem(24);
					border-radius: rem(5);
					box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2),
					0px 4px 50px rgba(0, 0, 0, 0.4);

					display: none;

					ul {
						display: flex;
						flex-direction: column;

						li {
							margin-bottom: rem(26);

							&:last-child {
								margin-bottom: 0;
							}

							a {
								white-space: nowrap;
								color: #5C5C5C;
								font-weight: 500;

								&:hover {
									color: #212121;
								}
							}
						}
					}
				}
			}

			.drop_menu.active {

				.item {
					span {
						color: white;
					}

					svg {
						path {
							stroke: white;
						}
					}
				}

				.drop_menu__list {
					display: block;
				}
			}

			.item {
				display: flex;
				align-items: center;
				margin-right: rem(30);

				&:hover {
					span {
						color: var(--c-white-80);
					}

					svg {
						path {
							stroke: var(--c-white-80);
						}
					}
				}

				&:last-child {
					margin-right: 0;
				}

				span {
					color: var(--c-white-50);
					font-weight: 500;
					margin-right: rem(3);
				}

				svg {
					path {
						stroke: var(--c-white-50);
					}
				}
			}
		}

		&_center {
			span {
				color: var(--c-white-80);
			}
		}

		&_right {
			.item {

				// margin-right: rem(13);

				&:hover {
					span {
						color: var(--c-white-80);
					}

					svg {
						path {
							stroke: var(--c-white-80);
						}
					}
				}

				&:first-child {
					margin-right: rem(13);
				}

				&:nth-child(2) {
					margin-left: rem(13);
					margin-right: rem(13);
				}

				&:last-child {
					margin-right: 0;
				}

				span {
					font-weight: 600;
					color: var(--c-white-50);
				}
			}

			.item_modal {

				&:hover {
					span {
						color: var(--c-accent);
					}
				}

				span {
					color: var(--c-accent2);
				}
			}
		}
	}

	&__bottom {
		// height: rem(135);
		backdrop-filter: blur(24px);
		background: linear-gradient(90.05deg, rgba(32, 32, 32, 0.63) 6.86%, rgba(255, 181, 113, 0.63) 99.97%);
		padding-bottom: rem(17);

		.container {
			display: flex;
			flex-direction: column;
			align-items: center;


		}

		&_logo {
			margin-top: rem(30);
			margin-bottom: rem(28);
		}

		&_links {
			width: 100%;
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;
			

			.header__nav {
				display: flex;
				align-items: center;

				ul {
					display: flex;
					flex-wrap: wrap;

					li {
						margin-right: rem(60);

						&:last-child {
							margin-right: 0;
						}

						a {
							color: rgba(255,255,255,.56);
							text-transform: uppercase;
							font-weight: 500;

							&:hover {
								color: white;
							}
						}
					}
					
				}

				&_drop {
					// position: relative;
					color: white !important;
					display: flex;
					align-items: center;

					svg {
						transform: rotate(180deg);
						margin-left: rem(6);
					}

				}

				&_drop.active {
					svg {
						transform: rotate(0deg);
					}
				}
			}

			.right {
				display: flex;
				align-items: center;

				.item {
					background-color: var(--c-gray-title);
					width: rem(29);
					height: rem(29);
					border-radius: rem(2);

					display: flex;
					align-items: center;
					justify-content: center;

					margin-right: rem(8);

					&:hover {
						transform: translateY(-3px);
					}

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}
	}

	&__drop_wrapper {
		position: absolute;
		top: rem(190);
		left: 0;
		z-index: 2;
		// height: rem(370);
		width: 100%;
		background-color: white;

		display: none;

		.top {
			display: flex;
			justify-content: space-between;

			padding-top: rem(20);
			padding-bottom: rem(18);
			border-bottom: rem(0.5) solid var(--c-black-30);

			ul {
				display: flex;
				flex-wrap: wrap;

				li {
					margin-right: rem(58);

					&:last-child {
						margin-right: 0;
					}

					a,
					button {
						font-weight: 500;
						color: var(--c-black-50);

						display: flex;
						align-items: center;

						&:hover {
							color: black;

							svg {
								path {
									stroke: black;
								}
							}
						}

						svg {
							margin-left: rem(8);

							path {
								stroke: var(--c-black-50);
							}
						}
					}
				}

				li.active {
					a,
					button {
						color: black;

						svg {
							transform: rotate(180deg);
							path {
								stroke: black;
							}
						}
					}
				}
			}
		}

		.bottom {
			&__tab {
				// display: flex;
				flex-wrap: wrap;
				justify-content: center;
				display: none;
				.item {
					padding: rem(40);
					border-right: rem(0.5) solid var(--c-black-30);

					display: flex;
					flex-direction: column;
					align-items: center;

					flex: 0 0 rem(270);
					height: rem(300);

					@include min1440 {
						flex: 0 0 rem(240);
						height: rem(270);
					}

					&:last-child {
						border-right: 0;
					}

					img {
						width: rem(192);
						height: rem(192);
						margin-bottom: rem(15);

						@include min1440 {
							width: rem(162);
							height: rem(162);
						}
					}

					span {
						font-size: rem(12);
						line-height: 140%;
						text-align: center;
					}
				}
			}

			&__tab.active {
				display: flex;
			}
		}
	}

	&__drop_wrapper.active {
		display: block;
	}
}

.header.header_small {
	height: rem(180);
	background: url(../img/header_bg_mini.jpg) no-repeat top center;
	background-size: cover;

	margin-bottom: rem(40);
}