.info {
	margin-bottom: rem(134);

	.container {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		// padding: 0 rem(195);
	}

	&__left {
		flex: 0 0 rem(517);
		max-width: rem(517);
		margin-right: rem(46);
	}

	&__right {
		flex: 0 0 rem(442);
		max-width: rem(442);

		.title {
			font-size: rem(40);
			font-family: var(--fontBrygada);
			font-weight: normal;
			font-style: italic;
			margin-bottom: rem(54);
		}

		.subtitle {
			display: flex;
			align-items: center;

			height: rem(100);
			padding-left: rem(26);
			margin-bottom: rem(26);

			border-left: rem(1) solid #6F6F6F;

			span {
				color: var(--c-accent2);
				font-size: rem(25);
				font-weight: normal;
				text-transform: uppercase;
				font-family: var(--fontSpartan);
			}
		}

		p {
			font-weight: 300;
			line-height: 195%;
			color: #2C2C2C;
		}
	}
}