.footer {
	background-color: #212121;

	
	padding-bottom: rem(30);

	.container {
		position: relative;
		display: flex;
		flex-wrap: wrap;
	}

	&__button_arrow {
		position: absolute;
		top: rem(100);
		right: rem(20);
		width: rem(50);
		height: rem(50);
		border-radius: 50%;

		display: flex;
		align-items: center;
		justify-content: center;
		background-color: white;

		// box-shadow: 0px 0px 10px rgba(174, 174, 174, 01), 0px 0px 3px rgba(0, 0, 0, 01);

		&:hover {
			transform: translateY(-10px);
		}

	}

	&__left, &__right {
		padding-top: rem(90);
		flex: 0 0 50%;
		max-width: 50%;
	}

	&__left {
		padding-bottom: rem(40);
		border-right: rem(1) solid var(--c-white-20);

		display: flex;
		flex-direction: column;

		.logo {
			margin-bottom: rem(38);

			align-self: flex-start;
		}
		.time {
			margin-bottom: rem(32);
			color: white;
			font-weight: 500;
		}

		.flex {
			margin-bottom: rem(24);

			.text {
				font-size: rem(12);
				line-height: 180%;
				font-style: italic;
				color: var(--c-white-50);

				max-width: rem(346);
			}

			.block_list {
				margin-right: rem(50);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&__right {

		display: flex;
		flex-wrap: wrap;
		flex-direction: column;
		// align-self: flex-start;
		margin-left: rem(190);

		flex: 0 0 rem(334);
		max-width: rem(334);

		height: rem(412);

		&_list {
			display: flex;
			flex-direction: column;
			margin-right: rem(90);

			&:last-child {
				margin-right: 0;
			}

			&:first-child {
				margin-bottom: rem(77);
			}

			.top {
				font-size: rem(16);
				line-height: 120%;
				font-weight: bold;
				color: white;
				margin-bottom: rem(28);

				&:hover {
					color: var(--c-accent);
				}
			}

			ul {
				li {
					margin-bottom: rem(14);

					&:last-child {
						margin-bottom: 0;
					}

					a {
						
						display: block;
						font-weight: 500;
						line-height: rem(17);
						color: var(--c-white-60);

						&:hover {
							color: var(--c-accent);
						}
					}
				}
			}
		}

	}


	&__bottom {
		border-top: rem(1) solid var(--c-white-20);

		flex: 0 0 100%;
		max-width: 100%;

		padding-top: rem(16);
		padding-bottom: rem(34);

		display: flex;
		justify-content: space-between;

		.text {
			flex: 0 0 rem(393);
			max-width: rem(393);

			font-size: rem(12);
			line-height: 180%;
			color: rgba(255,255,255,.45);
		}

		.link {
			flex: 0 0 rem(393);
			max-width: rem(393);

			font-size: rem(12);
			color: rgba(255, 255, 255, .45);
			text-decoration: underline;
			margin-top: rem(12);

			&:hover {
				text-decoration: none;
			}
		}

		&_right {
			display: flex;
			align-items: center;

			a {
				font-size: rem(12);
				color: var(--c-white-50);
				

				display: flex;
				align-items: center;

				img {
					margin-left: rem(7);
				}
			}

			img {
				// height: rem(20);
			}
		}
	}
}