:root {
	--container: 1480px;
	--c-accent: #C87224;
	--c-accent2: #EAA05C;

	--c-black-20: rgba(0, 0, 0, .2);
	--c-black-30: rgba(0, 0, 0, .3);
	--c-black-40: rgba(0, 0, 0, .4);
	--c-black-50: rgba(0, 0, 0, .5);
	--c-black-60: rgba(0, 0, 0, .6);
	--c-black-70: rgba(0, 0, 0, .7);
	--c-white-20: rgba(255, 255, 255, .2);
	--c-white-50: rgba(255,255,255,.5);
	--c-white-60: rgba(255, 255, 255, .6);
	--c-white-80: rgba(255, 255, 255, .8);

	--c-gray-text: #4F4F4F;
	--c-gray-title: #3E3E3E;

	--transition: 250ms ease;

	--fontSpartan: 'Spartan', sans-serif;
	--fontBrygada: 'Brygada1918', sans-serif;

}

* {
	outline: none;
	border: 0;
	margin: 0;
	padding: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

a, button {
	transition: var(--transition);

	* {
		transition: var(--transition);
	}
}

li {
	list-style: none;
}

body {
	position: relative;
	// font-size: rem(16);
	font-size: 14px;
	font-family: 'Inter', sans-serif;
	// height: 100%;
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;

	background-color: #F8F8F8;
}

.container {
	max-width: var(--container);
	padding-left: 20px;
	padding-right: 20px;
	margin: 0 auto;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.wrapper {
	margin-bottom: rem(130);
	
	.container {
		display: flex;
		flex-wrap: wrap;
		
	}

	&__right {
		flex: 1;
		min-width: 0;
		margin-left: rem(82);

		.title_36 {
			margin-bottom: rem(32);
		}

		&_col1 {
			flex: 0 0 calc(72% - 24px);
			max-width: calc(72% - 24px);

			margin-right: rem(24);
		}

		&_col1.mr-120 {
			flex: 0 0 calc(72% - 90px);
			max-width: calc(72% - 90px);

			margin-right: rem(90);
		}

		&_col2 {
			flex: 0 0 28%;
			max-width: 28%;
		}

	}
}

.grid {
	display: grid;
	grid-template-columns: calc((100% - var(--container))/2) 2.3fr 7.6fr calc((100% - var(--container))/2);

	@include min1440 {
		grid-template-columns: calc((100% - var(--container))/2) 2.6fr 7.6fr calc((100% - var(--container))/2);
	}

	&__left {
		grid-column: 1 / span 2;
	}

	&__right {
		min-width: 0;
		grid-column-end: span 2
	}
}