.advantages {

	margin-bottom: rem(140);

	.section_header {
		margin-bottom: rem(20);
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
		padding: rem(68) 0;
		border-bottom: rem(0.5) solid var(--c-black-40);
		border-top: rem(0.5) solid var(--c-black-40);
	}

	&__item {
		width: calc((100% - 360px) / 5);

		display: flex;
		flex-direction: column;

		margin-right: rem(90);

		&:last-child {
			margin-right: 0;
		}

		.icon {
			margin-bottom: rem(20);
		}

		.title {
			font-size: rem(18);
			font-weight: 600;
			line-height: 130%;
			color: var(--c-gray-title);
			margin-bottom: rem(13);
		}

		.text {
			line-height: 160%;
			color: var(--c-gray-text);
		}
	}

}