.review_item {
	display: flex;
	flex-wrap: wrap;

	padding: rem(50) rem(132) rem(32) rem(60);
	background-color: white;
	border-radius: rem(5);

	transition: var(--transition);

	margin-bottom: rem(24);

	* {
		transition: var(--transition);
	}

	&:hover {
		background-color: #3E3E3E;

		.text {
			color: white;
		}

		.username {
			color: rgba(255,255,255,.4);
		}
	}

	&__left {
		margin-right: rem(46);
	}

	&__right {
		flex: 0 0 rem(527);
		max-width: rem(527);

		.text {
			font-size: rem(18);
			font-style: italic;
			line-height: 160%;
			font-family: var(--fontBrygada);
			color: #212121;

			&:not(:nth-child(1)) {
				margin-top: rem(38);
			}

			
		}

		.username {
			margin-top: rem(20);
			color: rgba(33,33,33,.4);
			line-height: 160%;
			display: block;
		}
	}
}

.review_control {
	display: flex;
	flex-direction: column;

	margin-top: rem(64);

	&_button {
		align-self: flex-start;
		margin-bottom: rem(64);

		height: rem(54);
		display: flex;
		align-items: center;
		border: rem(1) solid black;

		padding: 0 rem(20);

		&:hover {
			background-color: black;

			svg {
				path {
					stroke: white;
				}
			}

			span {
				color: white;
			}
		}

		svg {
			path {
				stroke: black;
			}
		}

		span {
			margin-left: rem(10);
			font-weight: 500;
		}
	}

	&_page {
		display: flex;
		align-items: center;
		margin-bottom: rem(89);

		.arrow {
			&:first-child {
				margin-right: rem(28);
			}

			&:last-child {
				margin-left: rem(28);
			}
		}

		.page {
			width: rem(48);
			height: rem(48);
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: transparent;

			margin-right: rem(10);

			color: black;
			font-size: rem(16);
			font-weight: 500;

			&:hover {
				background-color: black;
				color: white;
			}

			&:last-child {
				margin-right: 0;
			}
		}

		.page.active {
			background-color: black;
			color: white;
		}

		.sep {
			font-size: rem(16);
			font-weight: 500;
			margin-right: rem(10);
		}
	}
}