.example_work {
	margin-bottom: rem(60);

	.container {
		position: relative;
	}

	.swiper {
		padding: rem(80) rem(80);
		margin: rem(-80) rem(-80);

		.swiper-slide {
			opacity: 0;
		}

		.swiper-slide-next,
		.swiper-slide-active {
			opacity: 1;
		}
	}

	.section_header {
		margin-bottom: rem(28);
		position: relative;
		z-index: 2;
	}

	.section-navigation {
		position: absolute;
		z-index: 2;
		top: rem(10);
		right: 0;
	}

}