.place_order {
	height: rem(178);
	width: 100%;

	background: url(../img/place_order_bg.jpg) no-repeat top center;
	background-size: cover;

	display: flex;
	align-items: center;

	margin-bottom: rem(168);

	.container {
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	}

	.section_header {
		.title {
			color: white;
		}
	}

	&__items {
		display: flex;
		align-items: center;
	}

	&__item {
		display: flex;
		flex-direction: column;

		margin-right: rem(36);

		@include min1440 {
			// margin-right: rem(16);
		}

		&:last-child {
			margin-right: 0;
		}

		svg {
			margin-bottom: rem(14);
		}

		b {
			color: white;
			line-height: 130%;
			font-weight: 600;

			// flex: 0 0 rem(156);
			// max-width: rem(156);
		}
	}
}