.modal_content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: var(--c-black-20);
	z-index: 50;

	// padding: rem(14);



	display: none;

	@include max768 {
		// display: flex;
		align-items: center;
		justify-content: center;
	}	

	.aside {
		display: block;
		flex: 0 0 100%;
		max-width: 100%;
		margin-right: 0;

		.aside__item-header {
			padding: rem(20) 0;
		}
	}

	&__sect-header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		margin: rem(14);

		padding: rem(20) rem(14);

		border-radius: rem(5);
		background-color: white;

		b {
			font-weight: 600;
			font-size: rem(16);
		}
	}

	&__bg {
		background-color: white;
		height: 100%;
		padding: rem(18) rem(14) rem(25) rem(14);
		overflow-y: auto;

		.modal_content__header {
			padding-bottom: rem(14);
			border-bottom: rem(0.5) solid var(--c-black-20);

			.title {
				font-size: rem(16);
			}

			.modal_close {
				position: absolute;
				top: rem(8);
				right: rem(8);
			}
		}
	}

	&__wrap {
		background-color: white;
		box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
			0px 4px 4px rgba(0, 0, 0, 0.25);
		border-radius: rem(5);
		// min-height: 100vh;
		// height: 100%;
		margin: rem(14);
		padding: rem(14) rem(10);

		@include max768 {
			width: rem(380);
			
			margin-top: rem(40);
		}
	}

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;

		margin-bottom: rem(15);

		.title {
			font-weight: bold;
		}
	}

	&__box {

		// overflow-y: auto;

		.field {
			margin-bottom: rem(14);
			height: rem(42);
			border-radius: rem(5);
			width: 100%;
			border: rem(1) solid var(--c-black-20);
			padding: 0 rem(20);

			font-size: rem(10);
			font-weight: 500;
			color: rgba(33,33,33,.5);

			@include placeholder {
				color: rgba(33, 33, 33, .5);
			}
		}

		.title {
			font-weight: bold;
			margin-bottom: rem(14);
			display: block;
		}

		&_form {
			margin-top: rem(32);

			.field {
				height: rem(54);
				border: rem(1) solid #FFB571;
				margin-bottom: rem(16);

				font-size: rem(16);
				font-weight: 500;
				line-height: 160%;
				color: black;

				@include placeholder {
					color: black;
				}
			}

			.button {
				height: rem(54);
				background-color: #FFB571;
				display: flex;
				align-items: center;

				padding: 0 rem(28);

				margin-bottom: rem(16);

				span {
					font-weight: 500;
					color: white;
				}
			}

			.field_checked {
				align-items: flex-start;
				
				.check {
					margin-top: rem(3);
					border-color: rgba(0,0,0,.8);
				}

				.text {
					color: rgba(0, 0, 0, .8);
					white-space: normal;
				}
			}
		}
	}

	&__favorites {
		display: flex;
		flex-direction: column;
		overflow-y: auto;

		.item {
			display: flex;
			flex-wrap: wrap;
			border: rem(0.5) solid var(--c-black-20);
			padding: rem(8) rem(6);
			border-radius: rem(5);

			margin-bottom: rem(10);

			transition: all 250ms ease;

			&:last-child {
				margin-bottom: 0;
			}

			&:hover {

				border-color: transparent;
				box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25),
					0px 4px 4px rgba(0, 0, 0, 0.25);

				.bottom a {
					opacity: 1;
				}

				.bottom .favorite_trash {
					opacity: 1;
				}
			}

			&__left {
				flex: 0 0 rem(82);
				height: rem(64);
				border-radius: rem(5);

				margin-right: rem(16);
				overflow: hidden;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			&__right {

				flex: 1;

				display: flex;
				flex-direction: column;

				.top {
					display: flex;
					flex-direction: column;

					b {
						font-weight: 600;
						font-size: rem(10);
						margin-bottom: rem(2);
					}

					span {
						font-size: rem(8);
					}
				}

				.bottom {
					margin-top: auto;

					display: flex;
					flex-wrap: wrap;
					align-items: center;
					justify-content: space-between;


					a {
						font-size: rem(8);
						color: var(--c-black-50);
						text-decoration: underline;

						opacity: 0;
					}

					.favorite_trash {
						transition: all 250ms ease;
						opacity: .4;
					}
				}
			}
		}
	}
}

.modal_content.active {
	display: flex;
}

.modal_change_city {

	.simplebar-scrollbar:before {
		opacity: 1 !important;
		left: -3px;
		background-color: #848484;
	}

	.simplebar-track {
		right: rem(4);

		margin: rem(20) 0;
	}

	.simplebar-track.simplebar-vertical {
		width: rem(4);
		background-color: rgba(196,196,196,.2);
	}

	.modal_content__wrap {
		height: rem(450);
	}

	.smart_menu__header-locale {
		margin-bottom: rem(60);

		display: flex;
		align-items: center;

		svg {
			margin-right: rem(6);
		}

		.text {
			font-weight: 500;
			color: #212121;
		}
	}

	.list_checked {
		display: flex;
		flex-direction: column;

		&__item {
			position: relative;
			height: rem(40);
			padding: 0 rem(20);
			display: flex;
			align-items: center;
			flex-direction: row-reverse;
			justify-content: space-between;
			border-radius: rem(5);

			input {
				display: none;

				&:checked {
					~.check {
						opacity: 1;

						&::before {
							content: "";
							position: absolute;
							top: 0;
							left: 0;
							width: 100%;
							height: 100%;
							background-color: rgba(225, 225, 225, .2);
						}
					}
				}
			}

			.check {
				transition: all 250ms ease;
				opacity: 0;

				i {
					font-size: rem(12);
				}
			}

			b {
				font-weight: 500;
				color: #212121;
			}
		}
	}
}

.modal_static {
	.modal_content__wrap {
		@include max768 {
			padding: rem(32) rem(40);
		}
	}

	.modal_content__header {
		.title {
			@include max768 {
				font-size: rem(24);
			}
		}
	}
}