.aside {
	display: flex;
	flex-direction: column;

	flex: 0 0 rem(243);
	max-width: rem(243);

	&__title {
		margin-bottom: rem(46);

		font-size: rem(24);
		line-height: 160%;
		font-weight: bold;
		color: #717171;
	}

	&__sect {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(32);

		&:last-child {
			.aside__sect_title {
				border-bottom: 0;
			}
		}

		&_title {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding-bottom: rem(14);
			margin-bottom: rem(18);
			border-bottom: rem(0.5) solid var(--c-black-40);

			span {
				font-weight: bold;
				color: #303030;
			}

			.arrow {

			}
		}

		&_list {

			display: none;

			ul {
				li {
					margin-bottom: rem(12);
					a {
						color: #484848;
						line-height: 160%;

						&:hover {
							color: var(--c-accent);
						}
					}

					a.active {
						color: var(--c-accent);
					}
				}
			}
		}
	}

	&__sect.active {
		.aside__sect_title {
			.arrow_accordion {
				background-color: var(--c-accent);

				svg {
					transform: rotate(180deg);
				}
			}
		}

		.aside__sect_list {
			display: block;
		}
	}
}

.arrow_accordion {
	width: rem(15);
	height: rem(15);
	border-radius: 50%;
	background-color: black;

	display: flex;
	align-items: center;
	justify-content: center;
}