

.accordion_item {
	display: flex;
	flex-direction: column;

	padding: rem(25) 0;

	border-bottom: rem(0.5) solid var(--c-black-40);

	&:first-of-type {
		border-top: rem(0.5) solid var(--c-black-40);
	}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;

		&:hover {
			.title {
				color: var(--c-accent);
			}
		}

		.title {
			font-size: rem(16);
			font-weight: 600;
			line-height: 112%;
			
		}


	}

	&__bottom {
		margin-top: rem(13);

		display: none;

		p {
			font-size: rem(16);
			line-height: 140%;
			color: var(--c-black-70);

			max-width: rem(544);
		}
	}
}

.accordion_item.active {
	.accordion_item__top {
		.arrow_accordion {
			background-color: var(--c-accent);

			svg {
				transform: rotate(180deg);
			}
		}
	}

	.accordion_item__bottom {
		display: block;
	}
}