.message_calc {

	

	.title_24 {
		margin-bottom: rem(27);
	}

	&__form {
		display: flex;
		height: rem(262);
		background: url(../img/bg_message.jpg) no-repeat;
		background-size: cover;
		
		.left, .right {
			flex: 0 0 50%;
			max-width: 50%;
		}

		.left {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;

			.title {
				margin-bottom: rem(36);
				font-weight: bold;
				font-style: italic;
				font-family: var(--fontBrygada);
				font-size: rem(18);
				line-height: 28px;
				color: white;

				max-width: rem(465);
			}

			.form_wrap {
				display: flex;
				flex-direction: column;

				&__line {
					display: flex;

					margin-bottom: rem(12);

					&:last-child {
						margin-bottom: 0;
					}

					.field {
						flex: 0 0 rem(306);
						max-width: rem(306);
						height: rem(54);
						background-color: white;
						border-radius: rem(5);
						border: rem(1) solid #FFB571;
						padding: 0 rem(20);
						box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);

						color: black;
						font-weight: 500;
						font-size: rem(16);

						margin-right: rem(18);

						@include placeholder {
							color: black;
						}
					}

					.button {
						height: rem(54);
						box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
						border-radius: rem(5);
						background-color: #FFB571;
						padding: 0 rem(28);

						display: flex;
						align-items: center;
						justify-content: center;

						span {
							color: white;
							white-space: nowrap;
							text-transform: lowercase;
						}
					}

					
				}
			}
		}

		.right {
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}