.assortment {

	margin-bottom: rem(140);

	.section_header {
		margin-bottom: rem(28);
	}

	&__items {
		display: flex;
		flex-wrap: wrap;
	}

	&__item {
		position: relative;
		width: calc((100% - 20px) / 3);

		height: rem(330);
		overflow: hidden;

		margin-right: rem(10);
		margin-bottom: rem(10);
		
		display: flex;
		justify-content: center;
		
		transition: var(--transition);

		&:hover {
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

			&::after {
				opacity: 1;
			}

			&::before {
				border-color: #FFB571;
				box-shadow: 0px 0px 2.5px #FFB571, 0px 0px 1.5px #FFB571;
			}

			.title {
				color: white;
				align-self: center;

				transform: translateY(50%);
			}

			svg {
				transform: translateY(100%);
			}
		}

		&::after {
			position: absolute;
			content: "";
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: linear-gradient(90deg, rgba(0, 0, 0, 0.77) 1.13%, rgba(94, 53, 16, 0.64) 100.05%);
			opacity: 0;
			transition: var(--transition);
			z-index: -1;
		}

		&::before {
			position: absolute;
			top: rem(12);
			left: rem(12);

			content: "";
			width: calc(100% - 24px);
			height: calc(100% - 24px);
			border: rem(1) solid white;
		}

		&:nth-child(3n) {
			margin-right: 0;
		}

		img {
			position: absolute;
			z-index: -1;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		.title {
			position: relative;
			z-index: 2;
			font-size: rem(16);
			font-weight: bold;
			font-family: var(--fontBrygada);
			color: var(--c-gray-title);

			transform: translateY(93.5%);
			margin-bottom: rem(8);
		}

		svg {
			position: absolute;
			bottom: 0;
			
		}
	}
}