.message {
	background-color: #212121;
	height: rem(147);

	.container {
		display: flex;
		justify-content: space-between;
	}

	&__left {
		img {
			vertical-align: middle;
		}
	}

	&__right {
		display: flex;
		align-items: center;
		
		&_info {
			margin-right: rem(56);
			display: flex;
			flex-direction: column;
			align-items: flex-end;

			.title {
				font-size: rem(24);
				line-height: 180%;
				font-weight: bold;
				color: white;
			}

			.text {
				line-height: 180%;
				color: white;
			}
		}

		&_form {
			display: flex;
			align-items: center;

			.field {
				height: rem(56);
				width: rem(300);
				background-color: white;
				padding: 0 rem(20);
				display: flex;
				align-items: center;

				font-size: rem(16);
				color: black;

				margin-right: rem(16);

				@include placeholder {
					color: black;
				}
			}

			.button {
				height: rem(56);
				background-color: #FFB571;

				display: flex;
				align-items: center;
				padding: 0 rem(28);

				&:hover {
					background-color: #EAA05C;
				}
				
				span {
					color: white;
					text-transform: lowercase;
				}
			}
		}
	}
}