.locale_select {
	display: flex;
	align-items: center;

	.icon {
		margin-right: rem(9);
	}

	.text {
		color: white;
		margin-right: rem(3);
	}

	.arrow {

	}
}

.swiper-page {
	display: flex;
	justify-content: center;

	.swiper-pagination-bullet {
		width: rem(48);
		height: rem(48);
		border-radius: 50%;
		display: flex;
		align-items: center;
		justify-content: center;

		background-color: transparent;

		font-size: rem(16);
		font-weight: 500;
		color: black;
		opacity: 1;

		margin: 0 rem(10);
	}

	.swiper-pagination-bullet-active {
		background-color: black;
		color: white;
	}
}

.section-navigation {
	display: flex;
	align-items: center;

	.link {
		font-weight: 500;
		margin-right: rem(32);
		text-transform: lowercase;
		color: var(--c-gray-text);

		&:hover {
			color: var(--c-accent);
		}
	}

	&__prev {
		margin-right: rem(10);
	}

	&__prev,
	&__next {
		width: rem(38);
		height: rem(38);
		border-radius: 50%;

		display: flex;
		align-items: center;
		justify-content: center;

		border: rem(1) solid #3E3E3E;

		&:hover {
			background-color: black;
			border-color: black;

			svg {
				path {
					stroke: white;
				}
			}
		}

		svg {
			path {
				stroke: #3E3E3E;
			}
		}
	}
}

.section_header {
	display: flex;
	flex-direction: column;

	.subtitle {
		display: flex;
		align-items: center;

		font-family: var(--fontBrygada);
		font-weight: bold;
		font-size: rem(14);
		letter-spacing: rem(3.4);
		text-transform: uppercase;
		color: var(--c-accent2);

		margin-bottom: rem(4);

		&::before {
			content: "";
			width: rem(60);
			height: rem(1.2);
			background-color: #FFB571;
			margin-right: rem(8);
		}
	}

	.title {
		color: var(--c-gray-title);
		font-size: rem(36);
		font-weight: 600;
		line-height: 156%;
	}
}

.button_border {
	padding: rem(14) rem(28);
	border: rem(1) solid #FFB571;

	&:hover {
		background-color: #FFB571;

		span {
			color: #141105;
		}
	}

	span {
		color: white;
		text-transform: lowercase;
	}
}

.button_black {
	padding: rem(14) rem(57);
	background-color: black;

	span {
		color: white;
		font-weight: 500;
		line-height: 160%;
	}
}

.button_transparent {
	padding: rem(14) rem(57);
	background-color: transparent;

	span {
		color: var(--c-gray-title);
		font-weight: 500;
		line-height: 160%;
	}
}

.block_list {
	display: flex;
	flex-direction: column;

	&__title {
		color: rgba(255,255,255,.45);
		line-height: 180%;
		white-space: nowrap;
		margin-bottom: rem(1);
	}

	&__phone {
		font-weight: 500;
		color: white;
		line-height: 180%;

		&:hover {
			text-decoration: underline;
		}
	}

	&__mail {
		font-weight: 500;
		color: white;
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}

	&__social {
		display: flex;
		flex-wrap: wrap;
		margin-top: rem(10);

		a {
			margin-right: rem(10);

			&:hover {
				opacity: .8;
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}
}

.page_control {
	display: flex;
	flex-direction: column;

	&_button {
		align-self: flex-start;
		margin-bottom: rem(52);

		height: rem(54);
		display: flex;
		align-items: center;
		border: rem(1) solid black;

		padding: 0 rem(20);

		&:hover {
			background-color: black;

			svg {
				path {
					stroke: white;
				}
			}

			span {
				color: white;
			}
		}

		svg {
			path {
				stroke: black;
			}
		}

		span {
			margin-left: rem(10);
			font-weight: 500;
		}
	}

	&_page {
		display: flex;
		align-items: center;
		margin-bottom: rem(89);

		.arrow {
			&:first-child {
				margin-right: rem(28);
			}

			&:last-child {
				margin-left: rem(28);
			}
		}

		.page {
			width: rem(48);
			height: rem(48);
			border-radius: 50%;
			display: flex;
			align-items: center;
			justify-content: center;
			background-color: transparent;

			margin-right: rem(10);

			color: black;
			font-size: rem(16);
			font-weight: 500;

			&:hover {
				background-color: black;
				color: white;
			}

			&:last-child {
				margin-right: 0;
			}
		}

		.page.active {
			background-color: black;
			color: white;
		}

		.sep {
			font-size: rem(16);
			font-weight: 500;
			margin-right: rem(10);
		}
	}
}

.field_checked {
	cursor: pointer;
	input {
		display: none;

		&:checked {

			~.check {
				&::before {
					opacity: 1;
				}
			}

		}
	}

	display: flex;
	align-items: center;

	.check {
		position: relative;
		flex: 0 0 rem(14);
		max-width: rem(14);
		height: rem(14);
		border-radius: rem(1.5);

		border: rem(1) solid white;

		margin-right: rem(14);

		&::before {
			content: "";
			position: absolute;
			top: rem(-3);
			right: rem(-2);
			width: rem(14);
			height: rem(14);
			background: url(../img/checked.svg) no-repeat;

			opacity: 0;
		}
	}

	.text {
		line-height: 150%;
		white-space: nowrap;
		color: white;
	}
}

.title_24 {
	font-size: rem(24);
	font-weight: 600;
	line-height: 156%;
	color: var(--c-gray-title);
}

.title_36 {
	font-size: rem(36);
	font-weight: 600;
	line-height: 156%;
	color: var(--c-gray-title);
}