@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap");
*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Spartan';
  src: local("Spartan Thin"), local("Spartan-Thin"), url("../fonts/Spartan/Spartan-Thin.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Brygada1918';
  src: local("Brygada1918-Regular"), local("Brygada1918-Regular"), url("../fonts/Brygada1918/Brygada1918-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Brygada1918';
  src: local("Brygada1918-Bold"), local("Brygada1918 Bold"), url("../fonts/Brygada1918/Brygada1918-Bold.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Brygada1918';
  src: local("Brygada1918-Bold"), local("Brygada1918 Bold"), url("../fonts/Brygada1918/Brygada1918-BoldItalic.woff2") format("woff2");
  font-weight: bold;
  font-style: italic;
  font-display: swap;
}

@font-face {
  font-family: 'Brygada1918';
  src: local("Brygada1918-SemiBold"), local("Brygada1918 SemiBold"), url("../fonts/Brygada1918/Brygada1918-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

:root {
  --container: 1480px;
  --c-accent: #C87224;
  --c-accent2: #EAA05C;
  --c-black-20: rgba(0, 0, 0, .2);
  --c-black-30: rgba(0, 0, 0, .3);
  --c-black-40: rgba(0, 0, 0, .4);
  --c-black-50: rgba(0, 0, 0, .5);
  --c-black-60: rgba(0, 0, 0, .6);
  --c-black-70: rgba(0, 0, 0, .7);
  --c-white-20: rgba(255, 255, 255, .2);
  --c-white-50: rgba(255,255,255,.5);
  --c-white-60: rgba(255, 255, 255, .6);
  --c-white-80: rgba(255, 255, 255, .8);
  --c-gray-text: #4F4F4F;
  --c-gray-title: #3E3E3E;
  --transition: 250ms ease;
  --fontSpartan: 'Spartan', sans-serif;
  --fontBrygada: 'Brygada1918', sans-serif;
}

* {
  outline: none;
  border: 0;
  margin: 0;
  padding: 0;
}

html, body {
  position: relative;
  overflow-x: hidden;
}

html {
  font-size: 100%;
}

button {
  background-color: transparent;
}

a {
  text-decoration: none;
}

a, button {
  transition: var(--transition);
}

a *, button * {
  transition: var(--transition);
}

li {
  list-style: none;
}

body {
  position: relative;
  font-size: 14px;
  font-family: 'Inter', sans-serif;
  overflow-x: hidden;
  background-color: #F8F8F8;
}

.container {
  max-width: var(--container);
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

.relative {
  position: relative;
}

.w-8\/12 {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

.w-7\/12 {
  flex: 0 0 58.333333%;
  max-width: 58.333333%;
}

.w-6\/12 {
  flex: 0 0 50%;
  max-width: 50%;
}

.w-5\/12 {
  flex: 0 0 41.666667%;
  max-width: 41.666667%;
}

.w-4\/12 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.block {
  display: block;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-col {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.flex-between {
  justify-content: space-between;
}

.items-center {
  align-items: center !important;
}

.items-start {
  align-items: flex-start !important;
}

.items-end {
  align-items: flex-end !important;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.wrapper {
  margin-bottom: 8.125rem;
}

.wrapper .container {
  display: flex;
  flex-wrap: wrap;
}

.wrapper__right {
  flex: 1;
  min-width: 0;
  margin-left: 5.125rem;
}

.wrapper__right .title_36 {
  margin-bottom: 2rem;
}

.wrapper__right_col1 {
  flex: 0 0 calc(72% - 24px);
  max-width: calc(72% - 24px);
  margin-right: 1.5rem;
}

.wrapper__right_col1.mr-120 {
  flex: 0 0 calc(72% - 90px);
  max-width: calc(72% - 90px);
  margin-right: 5.625rem;
}

.wrapper__right_col2 {
  flex: 0 0 28%;
  max-width: 28%;
}

.grid {
  display: grid;
  grid-template-columns: calc((100% - var(--container))/2) 2.3fr 7.6fr calc((100% - var(--container))/2);
}

@media (max-width: 1440px) {
  .grid {
    grid-template-columns: calc((100% - var(--container))/2) 2.6fr 7.6fr calc((100% - var(--container))/2);
  }
}

.grid__left {
  grid-column: 1 / span 2;
}

.grid__right {
  min-width: 0;
  grid-column-end: span 2;
}

.aside {
  display: flex;
  flex-direction: column;
  flex: 0 0 15.1875rem;
  max-width: 15.1875rem;
}

.aside__title {
  margin-bottom: 2.875rem;
  font-size: 1.5rem;
  line-height: 160%;
  font-weight: bold;
  color: #717171;
}

.aside__sect {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}

.aside__sect:last-child .aside__sect_title {
  border-bottom: 0;
}

.aside__sect_title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 0.875rem;
  margin-bottom: 1.125rem;
  border-bottom: 0.03125rem solid var(--c-black-40);
}

.aside__sect_title span {
  font-weight: bold;
  color: #303030;
}

.aside__sect_list {
  display: none;
}

.aside__sect_list ul li {
  margin-bottom: 0.75rem;
}

.aside__sect_list ul li a {
  color: #484848;
  line-height: 160%;
}

.aside__sect_list ul li a:hover {
  color: var(--c-accent);
}

.aside__sect_list ul li a.active {
  color: var(--c-accent);
}

.aside__sect.active .aside__sect_title .arrow_accordion {
  background-color: var(--c-accent);
}

.aside__sect.active .aside__sect_title .arrow_accordion svg {
  transform: rotate(180deg);
}

.aside__sect.active .aside__sect_list {
  display: block;
}

.arrow_accordion {
  width: 0.9375rem;
  height: 0.9375rem;
  border-radius: 50%;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.breadcrumbs {
  margin-bottom: 3.75rem;
}

.breadcrumbs .container {
  display: flex;
  align-items: center;
}

.breadcrumbs a {
  margin-right: 1.75rem;
  position: relative;
}

.breadcrumbs a::before {
  content: "";
  position: absolute;
  top: 0.4375rem;
  right: -1.25rem;
  background: url(../img/arrow_left.svg) no-repeat;
  width: 0.5rem;
  height: 0.4375rem;
}

.breadcrumbs span {
  font-size: 0.75rem;
  font-weight: 600;
  line-height: 160%;
  color: #4E4E4E;
}

.footer {
  background-color: #212121;
  padding-bottom: 1.875rem;
}

.footer .container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.footer__button_arrow {
  position: absolute;
  top: 6.25rem;
  right: 1.25rem;
  width: 3.125rem;
  height: 3.125rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}

.footer__button_arrow:hover {
  transform: translateY(-10px);
}

.footer__left, .footer__right {
  padding-top: 5.625rem;
  flex: 0 0 50%;
  max-width: 50%;
}

.footer__left {
  padding-bottom: 2.5rem;
  border-right: 0.0625rem solid var(--c-white-20);
  display: flex;
  flex-direction: column;
}

.footer__left .logo {
  margin-bottom: 2.375rem;
  align-self: flex-start;
}

.footer__left .time {
  margin-bottom: 2rem;
  color: white;
  font-weight: 500;
}

.footer__left .flex {
  margin-bottom: 1.5rem;
}

.footer__left .flex .text {
  font-size: 0.75rem;
  line-height: 180%;
  font-style: italic;
  color: var(--c-white-50);
  max-width: 21.625rem;
}

.footer__left .flex .block_list {
  margin-right: 3.125rem;
}

.footer__left .flex .block_list:last-child {
  margin-right: 0;
}

.footer__right {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-left: 11.875rem;
  flex: 0 0 20.875rem;
  max-width: 20.875rem;
  height: 25.75rem;
}

.footer__right_list {
  display: flex;
  flex-direction: column;
  margin-right: 5.625rem;
}

.footer__right_list:last-child {
  margin-right: 0;
}

.footer__right_list:first-child {
  margin-bottom: 4.8125rem;
}

.footer__right_list .top {
  font-size: 1rem;
  line-height: 120%;
  font-weight: bold;
  color: white;
  margin-bottom: 1.75rem;
}

.footer__right_list .top:hover {
  color: var(--c-accent);
}

.footer__right_list ul li {
  margin-bottom: 0.875rem;
}

.footer__right_list ul li:last-child {
  margin-bottom: 0;
}

.footer__right_list ul li a {
  display: block;
  font-weight: 500;
  line-height: 1.0625rem;
  color: var(--c-white-60);
}

.footer__right_list ul li a:hover {
  color: var(--c-accent);
}

.footer__bottom {
  border-top: 0.0625rem solid var(--c-white-20);
  flex: 0 0 100%;
  max-width: 100%;
  padding-top: 1rem;
  padding-bottom: 2.125rem;
  display: flex;
  justify-content: space-between;
}

.footer__bottom .text {
  flex: 0 0 24.5625rem;
  max-width: 24.5625rem;
  font-size: 0.75rem;
  line-height: 180%;
  color: rgba(255, 255, 255, 0.45);
}

.footer__bottom .link {
  flex: 0 0 24.5625rem;
  max-width: 24.5625rem;
  font-size: 0.75rem;
  color: rgba(255, 255, 255, 0.45);
  text-decoration: underline;
  margin-top: 0.75rem;
}

.footer__bottom .link:hover {
  text-decoration: none;
}

.footer__bottom_right {
  display: flex;
  align-items: center;
}

.footer__bottom_right a {
  font-size: 0.75rem;
  color: var(--c-white-50);
  display: flex;
  align-items: center;
}

.footer__bottom_right a img {
  margin-left: 0.4375rem;
}

.message_calc .title_24 {
  margin-bottom: 1.6875rem;
}

.message_calc__form {
  display: flex;
  height: 16.375rem;
  background: url(../img/bg_message.jpg) no-repeat;
  background-size: cover;
}

.message_calc__form .left, .message_calc__form .right {
  flex: 0 0 50%;
  max-width: 50%;
}

.message_calc__form .left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.message_calc__form .left .title {
  margin-bottom: 2.25rem;
  font-weight: bold;
  font-style: italic;
  font-family: var(--fontBrygada);
  font-size: 1.125rem;
  line-height: 28px;
  color: white;
  max-width: 29.0625rem;
}

.message_calc__form .left .form_wrap {
  display: flex;
  flex-direction: column;
}

.message_calc__form .left .form_wrap__line {
  display: flex;
  margin-bottom: 0.75rem;
}

.message_calc__form .left .form_wrap__line:last-child {
  margin-bottom: 0;
}

.message_calc__form .left .form_wrap__line .field {
  flex: 0 0 19.125rem;
  max-width: 19.125rem;
  height: 3.375rem;
  background-color: white;
  border-radius: 0.3125rem;
  border: 0.0625rem solid #FFB571;
  padding: 0 1.25rem;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
  color: black;
  font-weight: 500;
  font-size: 1rem;
  margin-right: 1.125rem;
}

.message_calc__form .left .form_wrap__line .field::-webkit-input-placeholder {
  color: black;
}

.message_calc__form .left .form_wrap__line .field:-moz-placeholder {
  color: black;
}

.message_calc__form .left .form_wrap__line .field::-moz-placeholder {
  color: black;
}

.message_calc__form .left .form_wrap__line .field:-ms-input-placeholder {
  color: black;
}

.message_calc__form .left .form_wrap__line .button {
  height: 3.375rem;
  box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
  border-radius: 0.3125rem;
  background-color: #FFB571;
  padding: 0 1.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.message_calc__form .left .form_wrap__line .button span {
  color: white;
  white-space: nowrap;
  text-transform: lowercase;
}

.message_calc__form .right img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.header {
  height: 60.3125rem;
  width: 100%;
  background: url(../img/header_bg.jpg) no-repeat top center;
  background-size: cover;
  margin-bottom: 7.0625rem;
}

.header__abs {
  margin-top: 9.375rem;
  display: flex;
  justify-content: center;
}

.header__top {
  height: 2.75rem;
  background-color: black;
}

.header__top .container {
  height: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__top_left {
  display: flex;
  align-items: center;
}

.header__top_left .drop_menu {
  position: relative;
  margin-right: 1.875rem;
}

.header__top_left .drop_menu__list {
  position: absolute;
  top: 2.625rem;
  left: -0.3125rem;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.99);
  padding: 1.5rem;
  border-radius: 0.3125rem;
  box-shadow: 0px 4px 5px rgba(0, 0, 0, 0.2), 0px 4px 50px rgba(0, 0, 0, 0.4);
  display: none;
}

.header__top_left .drop_menu__list ul {
  display: flex;
  flex-direction: column;
}

.header__top_left .drop_menu__list ul li {
  margin-bottom: 1.625rem;
}

.header__top_left .drop_menu__list ul li:last-child {
  margin-bottom: 0;
}

.header__top_left .drop_menu__list ul li a {
  white-space: nowrap;
  color: #5C5C5C;
  font-weight: 500;
}

.header__top_left .drop_menu__list ul li a:hover {
  color: #212121;
}

.header__top_left .drop_menu.active .item span {
  color: white;
}

.header__top_left .drop_menu.active .item svg path {
  stroke: white;
}

.header__top_left .drop_menu.active .drop_menu__list {
  display: block;
}

.header__top_left .item {
  display: flex;
  align-items: center;
  margin-right: 1.875rem;
}

.header__top_left .item:hover span {
  color: var(--c-white-80);
}

.header__top_left .item:hover svg path {
  stroke: var(--c-white-80);
}

.header__top_left .item:last-child {
  margin-right: 0;
}

.header__top_left .item span {
  color: var(--c-white-50);
  font-weight: 500;
  margin-right: 0.1875rem;
}

.header__top_left .item svg path {
  stroke: var(--c-white-50);
}

.header__top_center span {
  color: var(--c-white-80);
}

.header__top_right .item:hover span {
  color: var(--c-white-80);
}

.header__top_right .item:hover svg path {
  stroke: var(--c-white-80);
}

.header__top_right .item:first-child {
  margin-right: 0.8125rem;
}

.header__top_right .item:nth-child(2) {
  margin-left: 0.8125rem;
  margin-right: 0.8125rem;
}

.header__top_right .item:last-child {
  margin-right: 0;
}

.header__top_right .item span {
  font-weight: 600;
  color: var(--c-white-50);
}

.header__top_right .item_modal:hover span {
  color: var(--c-accent);
}

.header__top_right .item_modal span {
  color: var(--c-accent2);
}

.header__bottom {
  backdrop-filter: blur(24px);
  background: linear-gradient(90.05deg, rgba(32, 32, 32, 0.63) 6.86%, rgba(255, 181, 113, 0.63) 99.97%);
  padding-bottom: 1.0625rem;
}

.header__bottom .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header__bottom_logo {
  margin-top: 1.875rem;
  margin-bottom: 1.75rem;
}

.header__bottom_links {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__bottom_links .header__nav {
  display: flex;
  align-items: center;
}

.header__bottom_links .header__nav ul {
  display: flex;
  flex-wrap: wrap;
}

.header__bottom_links .header__nav ul li {
  margin-right: 3.75rem;
}

.header__bottom_links .header__nav ul li:last-child {
  margin-right: 0;
}

.header__bottom_links .header__nav ul li a {
  color: rgba(255, 255, 255, 0.56);
  text-transform: uppercase;
  font-weight: 500;
}

.header__bottom_links .header__nav ul li a:hover {
  color: white;
}

.header__bottom_links .header__nav_drop {
  color: white !important;
  display: flex;
  align-items: center;
}

.header__bottom_links .header__nav_drop svg {
  transform: rotate(180deg);
  margin-left: 0.375rem;
}

.header__bottom_links .header__nav_drop.active svg {
  transform: rotate(0deg);
}

.header__bottom_links .right {
  display: flex;
  align-items: center;
}

.header__bottom_links .right .item {
  background-color: var(--c-gray-title);
  width: 1.8125rem;
  height: 1.8125rem;
  border-radius: 0.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.5rem;
}

.header__bottom_links .right .item:hover {
  transform: translateY(-3px);
}

.header__bottom_links .right .item:last-child {
  margin-right: 0;
}

.header__drop_wrapper {
  position: absolute;
  top: 11.875rem;
  left: 0;
  z-index: 2;
  width: 100%;
  background-color: white;
  display: none;
}

.header__drop_wrapper .top {
  display: flex;
  justify-content: space-between;
  padding-top: 1.25rem;
  padding-bottom: 1.125rem;
  border-bottom: 0.03125rem solid var(--c-black-30);
}

.header__drop_wrapper .top ul {
  display: flex;
  flex-wrap: wrap;
}

.header__drop_wrapper .top ul li {
  margin-right: 3.625rem;
}

.header__drop_wrapper .top ul li:last-child {
  margin-right: 0;
}

.header__drop_wrapper .top ul li a,
.header__drop_wrapper .top ul li button {
  font-weight: 500;
  color: var(--c-black-50);
  display: flex;
  align-items: center;
}

.header__drop_wrapper .top ul li a:hover,
.header__drop_wrapper .top ul li button:hover {
  color: black;
}

.header__drop_wrapper .top ul li a:hover svg path,
.header__drop_wrapper .top ul li button:hover svg path {
  stroke: black;
}

.header__drop_wrapper .top ul li a svg,
.header__drop_wrapper .top ul li button svg {
  margin-left: 0.5rem;
}

.header__drop_wrapper .top ul li a svg path,
.header__drop_wrapper .top ul li button svg path {
  stroke: var(--c-black-50);
}

.header__drop_wrapper .top ul li.active a,
.header__drop_wrapper .top ul li.active button {
  color: black;
}

.header__drop_wrapper .top ul li.active a svg,
.header__drop_wrapper .top ul li.active button svg {
  transform: rotate(180deg);
}

.header__drop_wrapper .top ul li.active a svg path,
.header__drop_wrapper .top ul li.active button svg path {
  stroke: black;
}

.header__drop_wrapper .bottom__tab {
  flex-wrap: wrap;
  justify-content: center;
  display: none;
}

.header__drop_wrapper .bottom__tab .item {
  padding: 2.5rem;
  border-right: 0.03125rem solid var(--c-black-30);
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0 0 16.875rem;
  height: 18.75rem;
}

@media (max-width: 1440px) {
  .header__drop_wrapper .bottom__tab .item {
    flex: 0 0 15rem;
    height: 16.875rem;
  }
