.reviews {
	margin-bottom: rem(134);

	.container {
		display: flex;
		flex-direction: column;
		align-items: center;


	}

	&__header {
		display: flex;
		flex-direction: column;
		align-items: center;

		margin-bottom: rem(60);

		.title {
			color: var(--c-gray-title);
			font-size: rem(36);
			font-weight: 600;
			line-height: 156%;
			margin-bottom: rem(4);
		}

		.link {
			color: var(--c-gray-text);
			font-weight: 500;
			line-height: 156%;
		}
	}

	&__content {
		width: 100%;

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;

		&_center {
			flex: 0 0 rem(850);
			max-width: rem(850);

			.swiper {
				padding: rem(40);
				margin: rem(-40);

				.swiper-page {
					margin-top: rem(87);
				}
			}
		}
	}
}